export default function Icon({ w, h, color, rotate }) {
  const style = () => {
    return {
      top: "0px"
    };
  };

  return (
    <div>
      <svg width={w} height={h} viewBox="0 0 100 100">
        <path
          fill="none"
          transform={rotate}
          style={{
            WebkitTransformOrigin: "50%50%",
            MsTransformOrigin: "50%50%",
            transformOrigin: "50%50%"
          }}
          stroke={color}
          strokeWidth="4"
          d="M10 10L90 10 90 90"
        ></path>
      </svg>
    </div>
  );
}
